.btn {
  font-family: "Rajdhandi-Light";
  display: inline-block;
  font-weight: 400;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn-edit-bug {
  outline: solid;
  outline: rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0);
  background: transparent;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  width: 2vw;
}

.btn-primary {
  color: #fff;
  background-color: #212529;
  border-color: white;
  font-family: Rajdhandi-Regular;
  bottom: 30px;
}

.btn-add-bug {
  left: 10px;
  top: 10px;
  position: sticky;
  z-index: 100;
}

.add-bug-popup {
  position: fixed; /* position the popup in the center of the screen */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  box-shadow: 0px 0px 100vw #ccc;
  z-index: 100; /* make sure the popup is above other elements */
}

.add-bug-popup input,
.add-bug-popup textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid #ccc;
}

.add-bug-popup label {
  font-weight: Rajdhandi-Bold;
  margin-bottom: 8px;
  display: block;
}

.add-bug-popup .btn {
  width: 100%;
  padding: 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: 10px;
}

.add-bug-popup .btn:hover {
  background-color: #3e8e41;
}

