.arrowR {
  cursor: pointer;
  height: 1;
  left: 50%;
  position: relative;
  top: 0%;
  transform: translateX(-50%) translateY(-50%);
  transition: transform 0.1s;
  width: 50%;
}

.arrowR-top, .arrowR-bottom {
  background-color: #666;
  height: 4px;
  left: -5px;
  position: absolute;
  top: 50%;
  width: 100%;
}

.arrowR-top:after, .arrowR-bottom:after {
  background-color: white;
  content: "";
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.15s;
}

.arrowR-top {
  transform: rotate(45deg);
  transform-origin: bottom right;
}

.arrowR-top:after {
  left: 100%;
  right: 0;
  transition-delay: 0s;
}

.arrowR-bottom {
  transform: rotate(-45deg);
  transform-origin: top right;
}

.arrowR-bottom:after {
  left: 0;
  right: 100%;
  transition-delay: 0.15s;
}

.arrowR:hover .arrowR-top:after {
  left: 0;
  transition-delay: 0.15s;
}

.arrowR:hover .arrowR-bottom:after {
  right: 0;
  transition-delay: 0s;
}

.arrowR:active {
  transform: translateX(-50%) translateY(-50%) scale(0.9);
}

